import React from "react";
import Slider from 'rc-slider';
import "./MonthSlider.css"
import {Control, Controller, FieldValues} from "react-hook-form";

const MonthSlider = (props: { className?:string, label: string, name: string, control: Control<FieldValues, any>
        | undefined, watch?, setValue?, resetField?}) => {
    const months = [
        'Stycze', 'Luty', 'Marzec', 'Kwiecie',
        'Maj', 'Czerwiec', 'Lipiec', 'Sierpie',
        'Wrzesie', 'Padziernik', 'Listopad', 'Grudzie', null
    ];

    const labels = [
        '1. poowy stycznia',
        '2. poowy stycznia',
        '1. poowy lutego',
        '2. poowy lutego',
        '1. poowy marca',
        '2. poowy marca',
        '1. poowy kwietnia',
        '2. poowy kwietnia',
        '1. poowy maja',
        '2. poowy maja',
        '1. poowy czerwca',
        '2. poowy czerwca',
        '1. poowy lipca',
        '2. poowy lipca',
        '1. poowy sierpnia',
        '2. poowy sierpnia',
        '1. poowy wrzenia',
        '2. poowy wrzenia',
        '1. poowy padziernika',
        '2. poowy padziernika',
        '1. poowy listopada',
        '2. poowy listopada',
        '1. poowy grudnia',
        '2. poowy grudnia',
    ]

    const [minVal,maxVal] = props.watch ??  [0,24]

    React.useEffect(() => {
        if(props.setValue) {

            // props.resetField(props.name)
            props.setValue(props.name, props.watch, {shouldDirty: true})
            props.setValue(props.name, props.watch, {shouldTouch: true})
        }
    }, [props.watch]);


    const slicedMonths = months.slice(( minVal+1)/2,( maxVal)/2+1)

    months.forEach((element, index, arr) => {
        if (!slicedMonths.includes(element)) {
            arr[index] = null;
        }
    });

    return (
        <div className={props.className + "  p-3"}>
        <label>{props.label}</label>
            <div className={"justify-content-center align-self-center d-flex flex-column w-100"} >
            <Controller
                name={props.name}
                control={props.control}
                render={({ field }) => (
                    <>
                    <Slider style={{height:"80px"}}
                        pushable={true}
                        allowCross={false}
                        {...field}
                        range
                        min={minVal}
                        max={maxVal}
                        marks={
                        months.reduce((acc, month, index) => {
                            // @ts-ignore
                            acc[2*index] = month;
                            return acc;
                        }, {})}
                    />
                    <label className={"m-2 center text-center"} style={{fontSize:"0.9rem"}}>Wybrano okres: <br/> od pocztku {labels[field.value[0]]} <br/>do koca {labels[field.value[1]-1]}.</label>
                    </>
                )}
            />
            </div>
        </div>

    );
};

export default  MonthSlider;